input{
    margin: 10px;
    border: 0.5px solid rgb(34, 131, 200);
    height: 50px;
    width: 150px;
    text-indent: 10px;
    border-radius: 5px;
}

.buttons button{
    width: 100px;
    height: 40px;
    background: #325aa8;
    color: white;
    border-style: solid;
    border-radius: 5px;
    border-color: white;
    margin-left: 10px;
}
.buttons button:hover{
    background: #1E1F23;
    color: #ffffff;
    transition: 0.5s;
}

.details{
    display: flex;
    margin-top: 20px;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-items: center;
    padding-right: 240px;
    padding-top: 10px;
    padding-left: 10px;
    background: #f5f5f5;
    border-radius: 10px;
}